import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MovieComponent } from './components/movie/movie.component';
import { ShowComponent } from './components/show/show.component';
import { PrimeMoviesComponent } from './components/prime-movies/prime-movies.component';
import { PrimeShowsComponent } from './components/prime-shows/prime-shows.component';
import { HuluMoviesComponent } from './components/hulu-movies/hulu-movies.component';
import { HuluShowsComponent } from './components/hulu-shows/hulu-shows.component';
import { NetflixMoviesComponent } from './components/netflix-movies/netflix-movies.component';
import { NetflixShowsComponent } from './components/netflix-shows/netflix-shows.component';
import { SearchComponent } from './components/search/search.component'
import { WelcomeComponent } from './components/welcome/welcome.component';

const routes: Routes = [
	{ path: '', redirectTo: '/home', pathMatch: 'full' },
	{ path: 'home', component: WelcomeComponent },
	{ path: 'movies', component: MovieComponent },
	{ path: 'movies/netflix', component: NetflixMoviesComponent },
	{ path: 'movies/hulu', component: HuluMoviesComponent },
	{ path: 'movies/prime', component: PrimeMoviesComponent },
	{ path: 'shows', component: ShowComponent },
	{ path: 'shows/netflix', component: NetflixShowsComponent },
	{ path: 'shows/hulu', component: HuluShowsComponent },
	{ path: 'shows/prime', component: PrimeShowsComponent },
	{ path: 'search', component: SearchComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
