import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs'
import { Movie } from '../../services/Movie';
import { Show } from '../show/show';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
})
export class SearchComponent implements OnInit {
  message: string;
  movies: Movie[];
  shows: Show[];
  private baseURL = "http://54.226.171.15:8080/";
  private canRender = false;

  constructor(private http:Http) 
  { 
  	
  }

  ngOnInit() {
  	this.message = sessionStorage.getItem("search");
  	this.getAllMovies().subscribe(mov => {
  		this.movies = mov;
  	});

  	this.getAllShows().subscribe(sh => {
  		this.shows = sh;
  		this.canRender = true;
  	});
  }

  getAllMovies(): Observable<Movie[]> {
  	return this.http.get(this.baseURL + "search?set=movie&name=" + this.message).pipe(map((response: any) => response.json()));
  }

  getAllShows(): Observable<Show[]> {
  	return this.http.get(this.baseURL + "search?set=show&name=" + this.message).pipe(map((response: any) => response.json()));
  }
}
