import { Component, OnInit } from '@angular/core';
import { NodeConnectorService } from '../../services/node-connector-service.service';
import { Observable } from 'rxjs'
import { Movie } from '../../services/Movie';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-movie',
  templateUrl: './movie.component.html',
  styleUrls: ['./movie.component.css']
  //providers: [NodeConnectorService]
})
export class MovieComponent implements OnInit {
  movies: Movie[];
  private baseURL = "http://localhost:8080/";
  private canRender = false;

  constructor(private http:Http) { }

  ngOnInit() {
  	this.getAllMovies().subscribe(mov => {
  		this.movies = mov;
  		this.canRender = true;
  		console.log(this.movies);
  	});
  }

  getAllMovies(): Observable<Movie[]> {
  	return this.http.get(this.baseURL + "movies").pipe(map((response: any) => response.json()));
  }

}
