import { Component, OnInit } from '@angular/core';
import { NodeConnectorService } from '../../services/node-connector-service.service';
import { Observable } from 'rxjs'
import { Show } from '../show/show';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-hulu-shows',
  templateUrl: './hulu-shows.component.html',
  styleUrls: ['./hulu-shows.component.css']
})
export class HuluShowsComponent implements OnInit {
  shows: Show[];
  private baseURL = "http://54.226.171.15:8080/";
  private canRender = false;

  constructor(private http:Http) { }

  ngOnInit() {
  	this.getAllShows().subscribe(sh => {
  		this.shows = sh;
  		this.canRender = true;
  		console.log(this.shows);
  	});
  }

  getAllShows(): Observable<Show[]> {
  	return this.http.get(this.baseURL + "shows/hulu").pipe(map((response: any) => response.json()));
  }

}
