import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs'
import { Movie } from '../../services/Movie';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-hulu-movies',
  templateUrl: './hulu-movies.component.html',
  styleUrls: ['./hulu-movies.component.css']
})
export class HuluMoviesComponent implements OnInit {
  movies: Movie[];
  private baseURL = "http://54.226.171.15:8080/";
  private canRender = false;

  constructor(private http:Http) { }

  ngOnInit() {
  	this.getAllMovies().subscribe(mov => {
  		this.movies = mov;
  		this.canRender = true;
  		console.log(this.movies);
  	});
  }

  getAllMovies(): Observable<Movie[]> {
  	return this.http.get(this.baseURL + "movies/hulu").pipe(map((response: any) => response.json()));
  }
}
